.App {
  background-color: #282c34;
  color: white;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  padding: 1%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  flex-direction: column;
  
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.answer-box {
  margin: 0.25%;
  padding: 0.5%;
  border-radius: 15px;
  background-color: #3f4552;
  display: block;
}
.question-card {
  color: black;
  background-color: white;
  display: flex;
  flex-direction: column;
  margin: 2%;
  padding: 2%;
}
.accordion-section {
  padding: 2%;
}
.score-section {
  display: flex;
  flex-direction: column;
  padding: 2%;
}
.score-section p {
  margin: 1%;
}
.score-section button {
  width: max-content;
  margin: 1%;
}